import EventEmitter from 'events'
import Events from '../../core/events'
import platform from '../../core/utils/platform'
import * as diskCache from '../../common/idb-keyval/index'
import Segment from "../../core/segment";

const KEY_METADATA_SIZE = 'size';

class SegmentStore extends EventEmitter {
    constructor(engine, config) {
        super();

        this.logger = config.logger;
        this.logger.info(`use SegmentStore`)
        this.channel = engine.channel;
        const device = engine.browserInfo.device;
        // 
        this.maxBufSize = device === platform.device.PC_WEB || device === platform.device.PC_NATIVE ?
            config.diskCacheLimit.pc : config.diskCacheLimit.mobile;
        /* segment
        sn: number
        segId: string
        data: Buffer
        size: string
        fromPeerId: string
         */
        // this.id2Sn = new Map();                //segIdsn             segId -> sn
        this.overflowed = false;               //
    }

    async setupStore() {
        if (navigator.storage) {
            const estimate = await navigator.storage.estimate(),
                // calculate remaining storage in MB
                available = Math.floor((estimate.quota - estimate.usage));
            if (available < this.maxBufSize) {
                this.maxBufSize = available  - 10*1024*1024;
            }
            // console.warn(`${ available } Byte remaining this.maxBufSize ${this.maxBufSize}`);
        }
        return new Promise((resolve, reject) => {
            if (this.maxBufSize < 300*1024*1024) {
                reject(`disk storage not enough`)
                return
            }
            const storeFuns = diskCache.createStore(this.channel)
            this.segmentsStore = storeFuns[0];
            this.id2SnStore = storeFuns[1];
            this.metaStore = storeFuns[2];
            const timer = setTimeout(() => {
                reject('setupStore timeout')
            }, 250);
            this._clearDisk(this.channel).then(() =>  {
                this._initMetaStore().then(() => {
                    clearTimeout(timer);
                    resolve()
                }).catch(e => {
                    reject(e)
                });
            }).catch(e => {
                reject(e)
            })
        })
    }

    _initMetaStore() {
        return new Promise((resolve, reject) => {
            diskCache.get(KEY_METADATA_SIZE, this.metaStore).then(size => {
                if (!size) {
                    this.logger.info('init MetaStore size');
                    diskCache.set(KEY_METADATA_SIZE, 0, this.metaStore);
                }
                resolve()
            }).catch(e => {
                reject()
            })
        })

    }

    currBufSize() {
        return diskCache.get(KEY_METADATA_SIZE, this.metaStore);
    }

    async hasSegOfId(segId) {
        const sn = await diskCache.get(segId, this.id2SnStore);
        return new Promise((resolve, reject) => {
            if (!sn) {
                resolve(false);
                return
            }
            diskCache.get(sn, this.segmentsStore).then((segs) => {
                // console.warn(segs)
                if (segs && segs.length > 0 && segs.some(seg => seg.segId === segId)) {
                    // console.warn(`resolve(true)`)
                    resolve(true)
                } else {
                    // console.warn(`resolve(false)`)
                    resolve(false)
                }
            }).catch((e) => {
                this.logger.error(e)
                resolve(false)
            })
        })
    }

    async getSegById(segId) {
        if (!segId) return null;
        const sn = await diskCache.get(segId, this.id2SnStore);
        return new Promise((resolve, reject) => {
            if (!sn) {
                resolve(null);
                return
            }
            diskCache.get(sn, this.segmentsStore).then((segs) => {
                if (segs && segs.length > 0) {
                    resolve(Segment.fromSegment(segs[0]))
                } else {
                    resolve(null)
                }
            }).catch((e) => {
                this.logger.error(e)
                resolve(null)
            })
        })
    }

    getSegIdBySN(sn) {
        return new Promise((resolve, reject) => {
            diskCache.get(sn, this.segmentsStore).then((segs) => {
                if (segs && segs.length > 0) {
                    resolve(segs[0].segId)
                } else {
                    resolve(null)
                }
            }).catch((e) => {
                this.logger.error(e)
                resolve(null)
            })
        })
    }

    putSeg(segment) {
        // if (this._segPool.has(segment.sn)) return;
        this._addSeg(segment);
    }

    _addSeg(seg) {
        const { logger } = this;
        const { segId, sn, size } = seg;
        diskCache.set(segId, sn, this.id2SnStore);

        diskCache.get(sn, this.segmentsStore).then((segs) => {
            if (segs) {
                if (segs.filter(seg => seg.segId === segId).length === 0) {
                    segs.push(this._segmentToCache(seg))
                    diskCache.set(sn, segs, this.segmentsStore).then(() => {
                        this._increaseBufSize(seg.data.byteLength);
                    }).catch((e) => {
                        this.logger.error(e)
                    })
                }
            } else {
                diskCache.set(sn, [this._segmentToCache(seg)], this.segmentsStore).then(() => {
                    this._increaseBufSize(seg.data.byteLength);
                }).catch((e) => {
                    this.logger.error(e)
                })
            }
        }).catch((e) => {
            this.logger.error(e)
        })

        this.emit(`${Events.BM_ADDED_SN_}${seg.sn}`, seg);
        this.emit(Events.BM_SEG_ADDED, seg);
    }

    async _trimDisk() {
        const { logger } = this;
        let currentSize = await this.currBufSize()
        // console.warn(`_trimDisk currentSize ${currentSize}`)
        if (currentSize < this.maxBufSize) return
        diskCache.keys(this.segmentsStore).then(async (keys) => {
            const sorted = keys.sort((a, b) => a-b);  // 
            let count = 0;
            do {
                if (count++ > 10) {
                    console.error('too much loops in SegmentStore');
                    break
                }
                // buffer
                const oldestSN = sorted.shift();
                if (oldestSN === undefined) {
                    logger.error(`lastSN not found`);
                    continue;
                }
                const nextSN = sorted[0];
                const oldestSegs = await diskCache.get(oldestSN, this.segmentsStore);
                if (!oldestSegs) {
                    logger.error(`lastSeg not found`);
                    continue;
                }
                let size = 0;
                oldestSegs.forEach(oldestSeg => {
                    size += oldestSeg.data.byteLength;
                })
                diskCache.del(oldestSN, this.segmentsStore).then(() => {
                    this._decreaseBufSize(parseInt(size));
                });
                oldestSegs.forEach(oldestSeg => {
                    diskCache.del(oldestSeg.segId, this.id2SnStore);
                    logger.info(`pop seg ${oldestSeg.segId} size ${oldestSeg.data.byteLength}`);
                    this.emit(Events.BM_LOST, {sn: oldestSN, segId: oldestSeg.segId, next: nextSN, level: oldestSeg.level});
                })
                currentSize = await this.currBufSize();
                logger.info(`pop sn ${oldestSN} size ${size} currBufSize ${currentSize}`);
                if (!this.overflowed) this.overflowed = true;
            } while (currentSize >= this.maxBufSize)
        })
    }

    _decreaseBufSize(byteLength) {
        diskCache.get(KEY_METADATA_SIZE, this.metaStore).then(size => {
            if (size) {
                diskCache.set(KEY_METADATA_SIZE, size - byteLength, this.metaStore);
            }
        }).catch(e => {
            this.logger.error(e);
        })
    }

    _increaseBufSize(byteLength) {
        diskCache.get(KEY_METADATA_SIZE, this.metaStore).then(size => {
            diskCache.set(KEY_METADATA_SIZE, size + byteLength, this.metaStore);
            this._trimDisk();
        }).catch(e => {
            this.logger.error(e);
        })
    }

    _segmentToCache(seg) {
        return {
            data: seg.data,
            level: seg.level,
            segId: seg.segId,
            sn: seg.sn,
        }
    }

    clear() {
        this.logger.warn(`clear segment cache`);
        // this.id2Sn.clear();
        this._clearDisk();
    }

    // disk
    _clearDisk(excluded) {
        // console.warn(`_clearDisk`)
        return diskCache.clearAll(excluded)
    }

    destroy() {
        this.clear();
        this.removeAllListeners();
    }
}

export default SegmentStore;
