import { delimiter, arrayDelimiter, strToBytes, candTypeMap, char2SdpType } from './util'

export default function (packed, sessionId) {
    const type = char2SdpType(packed.substr(0, 1))
    if (!type) return null
    if (type.type === 'candidate') {
        return unpackCand(packed)
    }
    return unpackSdp(type.type, sessionId, packed, type.trickle)
}

function unpackCand(packed) {
    let item = unpackCandItem(packed.substr(1), 100)
    if (!item) return null
    return {
        type: "candidate",
        candidate: {
            candidate: `candidate:${item}`,
            sdpMLineIndex: 0,
            sdpMid: "0",
        }
    }
}

function unpackSdp(type, sessionId, packed, trickle) {
    const fingerprint = strToBytes(packed.slice(1, 1 + 32)).map(byte => ('0' + byte.toString(16)).slice(-2))
    const remain = packed.substr(33)
    let candidatesStr, ufrag, password
    if (trickle) {
        [ufrag, password] = remain.split(delimiter)
    } else {
        [candidatesStr, ufrag, password] = remain.split(delimiter)
    }
    const candidates = trickle ? [] : candidatesStr.split(arrayDelimiter)
    const sdpParts = [
        'v=0',
        `o=- ${sessionId} 2 IN IP4 127.0.0.1`,
        's=-',
        't=0 0',
        'a=group:BUNDLE 0',
        'a=msid-semantic: WMS',
        'm=application 9 UDP/DTLS/SCTP webrtc-datachannel',
        'c=IN IP4 0.0.0.0',
        'a=mid:0',
        'a=sctp-port:5000',
        `a=setup:${type === 'answer' ? 'active' : 'actpass'}`,
        `a=ice-ufrag:${ufrag}`,
        `a=ice-pwd:${password}`,
        `a=fingerprint:sha-256 ${fingerprint.join(':').toUpperCase()}`,
    ]
    if (trickle) {
        sdpParts.push(`a=ice-options:trickle`)
    }
    let priority = 100
    for (const cand of candidates) {
        let item = unpackCandItem(cand, priority)
        if (!item) continue
        priority --
        sdpParts.push(`a=candidate:${item}`)
    }
    return {
        type,
        sdp: sdpParts.join('\r\n') + '\r\n'
    }
}

function unpackCandItem(cand, priority) {
    let ip, port, foundation
    const candType = candTypeMap[cand.substr(0, 1)]
    const infos = cand.substr(1).split(' ')
    if (infos.length === 2) {
        // ipv4
        foundation = infos[0]
        const addr = infos[1].split(':')
        // ip = strToBytes(addr.substr(0, 4)).join('.')
        ip = [(addr[0] >> 24) & 0xff, (addr[0] >> 16) & 0xff, (addr[0] >> 8) & 0xff, addr[0] & 0xff].join('.')
        port = addr[1]
    } else {
        [foundation, ip, port] = infos
    }
    // console.warn(`ip ${ip} port ${port}`)
    return `${[
        foundation,
        1, // component id
        'udp', // transport
        priority,
        ip,
        port,
        'typ', candType, // should be okay, maybe
    ].join(' ')}`
}
