import URLToolkit from 'url-toolkit'

export function isBlockType(url, blackList) {
    const urlObj = URLToolkit.parseURL(url);
    const mediaType = urlObj.path.substring(urlObj.path.lastIndexOf('.')+1);
    // console.warn(`mediaType ${mediaType}`);
    // const extname = path.extname(url).toLowerCase();
    return blackList.indexOf(mediaType) !== -1;

}

export function createLoadStats() {
    const now = performance.now();
    return {
        // compat with HLS < 1.0.0
        trequest: now,
        tfirst: 0,
        tload: 0,

        aborted: false,
        loaded: 0,
        retry: 0,
        total: 0,
        chunkCount: 0,
        bwEstimate: 0,
        loading: { start: now, first: 0, end: 0 },
        parsing: { start: 0, end: 0 },
        buffering: { start: 0, first: 0, end: 0 }
    }
}

export function updateLoadStats(stats, size) {
    let trequest, tfirst, tload, loaded, total;
    const now = performance.now();
    trequest = now - 300;
    tfirst = now - 200;
    tload = now;
    stats.trequest = trequest;
    stats.tfirst = tfirst;
    stats.tload = tload;
    stats.loading = {
        first: trequest,
        start: tfirst,
        end: tload
    };
    loaded = total = size;
    stats.loaded = loaded;
    stats.total = total;
}
