
const players = {
    'DPlayer': 'dplayer',
    'CBPlayer': 'cbplayer',
    'jwplayer': 'jwplayer',
    'videojs': 'videojs',
    'Clappr': 'clappr',
    'ckplayer': 'ckplayer',
    'MediaElementPlayer': 'mediaelement',
    'MediaElement': 'mediaelement',
    'TcPlayer': 'tcplayer',
    'flowplayer': 'flowplayer',
    'Chimee': 'chimee',
    'ChimeePlayer': 'chimee',
    'HlsJsPlayer': 'xgplayer',
    'fluidPlayer': 'fluidplayer',
    'OpenPlayer': 'openplayer',
    'Plyr': 'plyr',
    'Playerjs': 'playerjs',
    'Aliplayer': 'aliplayer',
    'shaka': 'shakaplayer',
};

export default function () {
    let ret;
    for (let player in players) {
        if (window[player]) {
            ret = players[player];
            break;
        }
    }
    return ret;
}
