export function getQueryParam(name) {
    return new URL(location.href).searchParams.get(name)
}

// url
export function updateQueryStringParam(uri, key, value) {
    // if(!value) {
    //     return uri;
    // }
    var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
    var separator = uri.indexOf('?') !== -1 ? "&" : "?";
    if (uri.match(re)) {
        return uri.replace(re, '$1' + key + "=" + value + '$2');
    }
    else {
        return uri + separator + key + "=" + value;
    }
}

//  
export function getCurrentTs() {

    return Date.parse(new Date()) / 1000;
}

// minNummaxNum
export function randomNum(minNum, maxNum) {
    return parseInt(Math.random() * ( maxNum - minNum + 1 ) + minNum, 10);
}

// check peersdelay
export function calCheckPeersDelay(peerNum) {
    if (peerNum === 0) return CHECK_PEERS_INTERVAL;
    return 0.33*peerNum + 0.67;         // peer10
}

export function performRangeRequest(uri, range, xhrSetup) {
    const xhr = new XMLHttpRequest();
    return new Promise(((resolve, reject) => {
        xhr.open('GET', uri, true);
        xhr.responseType = 'arraybuffer';
        xhr.timeout = 3500;
        xhr.onreadystatechange = (event) => {
            const readyState = xhr.readyState;
            if (readyState === 4) {
                const status = xhr.status;
                if (status >= 200 && status < 300) {
                    resolve(xhr.response)
                } else {
                    reject(`status ${status}`);
                }
            }
        }
        xhr.onerror = (event) => {
            reject(`request error`);
        };
        xhr.ontimeout = (event) => {
            reject('timeout');
        };
        xhr.setRequestHeader('Range', range || 'bytes=0-0');
        if (xhrSetup) {
            xhrSetup(xhr, uri);
        }
        xhr.send();
    }));
}

