export function navLang() {
    var lang = navigator.language || navigator.userLanguage //IE
    // lang = lang.substr(0, 2) //lang2
    return lang === 'zh-CN' ? 'cn' : 'en'
}

export function dontWaitFor(promise) {
    // Effective no-op.
    promise.then(() => {});
}

export function timeout(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
}

export function getBrowserRTC () {
    if (typeof window === 'undefined') return null
    var wrtc = {
        RTCPeerConnection: window.RTCPeerConnection || window.mozRTCPeerConnection ||
            window.webkitRTCPeerConnection,
        RTCSessionDescription: window.RTCSessionDescription ||
            window.mozRTCSessionDescription || window.webkitRTCSessionDescription,
        RTCIceCandidate: window.RTCIceCandidate || window.mozRTCIceCandidate ||
            window.webkitRTCIceCandidate
    }
    if (!wrtc.RTCPeerConnection) return null
    return wrtc
}

export function copyBuffer(source) {
    const payloadBuf = Buffer.from(source);
    const targetBuffer = new Buffer(source.byteLength);
    payloadBuf.copy(targetBuffer);
    return targetBuffer
}

export function getMaxSequence(m3u8) {
    const lines = m3u8.split('\n');
    let start = 0;
    const tsTag = '#EXTINF';
    let count = 0;
    for (let line of lines) {
        const match = (/^#EXT-X-MEDIA-SEQUENCE:?(\-?[0-9.]*)?/).exec(line);
        if (match) {
            if (match[1]) {
                start = parseInt(match[1], 10);
                break
            }
        }
    }
    for (let line of lines) {
        if (line.startsWith(tsTag)) {
            count ++;
        }
    }
    return start+count-1
}

export function isHttps() {
    return location.protocol.startsWith('https')
}

export function isInteger(obj) {
    return typeof obj === 'number' && obj%1 === 0
}

