// fromsinkpacketSize
export function splitBytes(sink, from) {
    const packetSize = Peer.defaultPacketSize;
    const dataSize = sink.byteLength - from;
    const bufList = [];
    let offset = from;
    let packetsCompleted = Math.floor(dataSize / packetSize);
    let remainder = dataSize % packetSize;
    for (let i=0; i < packetsCompleted; i++) {
        const buffer = Buffer(packetSize);
        sink.copy(buffer, 0, offset, offset + packetSize)
        bufList.push(buffer);
        offset += packetSize;
    }
    if (remainder > 0) {
        const buffer = Buffer(remainder);
        sink.copy(buffer, 0, offset, offset + remainder)
        bufList.push(buffer);
    }
    return bufList
}


