/**
 * 
 */
export const setItem = (key, value) => {
    //  JSON 
    if (typeof value === 'object') {
        value = JSON.stringify(value)
    }
    localStorage.setItem(key, value)
}

/**
 * 
 */
export const hasItemUnexpired = key => {
    const data = localStorage.getItem(key)
    try {
        const item = JSON.parse(data)
        if (item.duration && item.startTime) {
            let date = new Date().getTime();
            // if (date - item.startTime >= item.duration) {
            //     console.warn(`ipdate expired`)
            // }
            return date - item.startTime < item.duration
        } else {
            return false
        }
    } catch (err) {
        return false
    }
}

/**
 * 
 */
export const getItem = key => {
    const data = localStorage.getItem(key)
    try {
        const item = JSON.parse(data)
        if (item.value) {
            return item.value
        } else {
            return item;
        }
    } catch (err) {
        return data
    }
}

/**
 * 
 */
export const removeItem = key => {
    localStorage.removeItem(key)
}

/**
 * 
 */
export const removeAllItem = () => {
    localStorage.clear()
}

/**
 * ms
 */
export const setItemWithExpiration = (key, value, duration) => {
    let obj = {
        value,
        duration,
        startTime: new Date().getTime()//
    }
    setItem(key, obj)
}
