import { noop } from './tool-funs';

const logMap = {
    debug: 0,
    info: 1,
    warn: 2,
    error: 3,
    none: 4,
};

class Logger {
    constructor(logLevel) {
        this.logLevel = logLevel;
        this.onlineDebug = false;
        console.debug = console.log;

        if ((logLevel === 'debug' || logLevel === 'info') && __RELEASE__) {
            this.logLevel = __MIN_LOG_LEVEL__;
        }

        if (logLevel === true) {
            this.logLevel = 'warn';
        } else if (logLevel === false) {
            this.logLevel = 'none';
        } else if (!(logLevel in logMap)) {
            this.logLevel = 'error';   // error
        }
        this.resetLogger();
    }

    enableDebug() {
        this.onlineDebug = true;
        for (let key in logMap) {
            this[key] = console[key];
        }
    }

    resetLogger() {
        this.onlineDebug = false;
        for (let key in logMap) {
            if (logMap[key] < logMap[this.logLevel]) {
                this[key] = noop;
            } else {
                this[key] = console[key];
            }
        }
    }

    get isDebugLevel() {
        return logMap[this.logLevel] <= 2 || this.onlineDebug;
    }

}

export default Logger;
