/** Char used to separate non-fixed width parts. */
export const delimiter = '|'
export const arrayDelimiter = ','
// h:host s:srflx p:prflx r:relay
export const candTypeMap = {
    h: 'host',
    s: 'srflx',
    p: 'prflx',
    r: 'relay',
}

export const sdpType2Char = (type, trickle) => {
    let ret
    switch (type) {
        case 'candidate':
            ret = 'c'
            break
        case 'offer':
            ret = trickle ? 'q' : 'o'
            break
        case 'answer':
            ret = trickle ? 's' : 'a'
            break
    }
    return ret
}

export const char2SdpType = (char) => {
    let ret = {}
    switch (char) {
        case 'o':
            ret = {
                type: 'offer'
            }
            break
        case 'q':
            ret = {
                type: 'offer',
                trickle: true,
            }
            break
        case 'a':
            ret = {
                type: 'answer'
            }
            break
        case 's':
            ret = {
                type: 'answer',
                trickle: true,
            }
            break
        case 'c':
            ret = {
                type: 'candidate',
                trickle: true,
            }
            break
    }
    return ret
}

const charOffset = 0

/** Converts an array of bytes to a 'human friendly' string. */
export const bytesToStr = (bytes) =>
    typeof bytes === 'number'
        ? bytesToStr([bytes])
        : String.fromCharCode(...bytes.map(byte => byte + charOffset))

/** Convert a 'human friendly' string to an array of bytes. */
export const strToBytes = (str) =>
    str.split('').map(char => char.charCodeAt(0) - charOffset)

export function parseCand(candStr) {
    const reg = /(\S*) (\d*) (\S*) (\d*) (\S*) (\d*) typ (\S*)(?: raddr (\S*) rport (\d*))?(?: tcptype (\S*))?(?: generation (\d*))?(?: ufrag (\S*))?(?: network-id (\d*))?(?: network-cost (\d*))?/
    const [,foundation, component, transport, priority, ip, port, type, raddr, rport, tcptype, generation, ufrag, networkId, networkCost] = reg.exec(candStr)
    return {
        foundation,
        component,
        transport,
        priority,
        ip,
        port,
        type,
        raddr,
        rport,
        tcptype,
        generation,
        networkId,
        networkCost,
        ufrag,
    }
}
