
const STORE_NOT_INIT = 'store not init'

export function promisifyRequest(request) {
    return new Promise((resolve, reject) => {
        request.oncomplete = request.onsuccess = () => resolve(request.result);
        request.onabort = request.onerror = () => reject(request.error);
    });
}

export function createStore(dbName) {
    // console.warn(`createStore`)
    const stores = ['segments', 'id2Sn', 'metadata']
    const request = indexedDB.open(dbName);
    request.onupgradeneeded = () => {
        const db = request.result;
        // console.warn(db.objectStoreNames)
        stores.forEach(storeName => {
            // console.warn(`createStore ${storeName}`)
            db.createObjectStore(storeName);
        })

    }
    const dbp = promisifyRequest(request);
    return stores.map(storeName => (txMode, callback) =>
        dbp.then((db) =>
            callback(db.transaction(storeName, txMode).objectStore(storeName)),
        ))
}

/**
 * Get a value by its key.
 */
export function get(key, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readonly', (store) => promisifyRequest(store.get(key)));
}

/**
 * Set a value with a key.
 *
 */
export function set(key, value, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readwrite', (store) => {
        store.put(value, key);
        return promisifyRequest(store.transaction);
    });
}

/**
 * Set multiple values at once. This is faster than calling set() multiple times.
 * It's also atomic  if one of the pairs can't be added, none will be added.
 *
 * */
export function setMany(entries, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readwrite', (store) => {
        entries.forEach((entry) => store.put(entry[1], entry[0]));
        return promisifyRequest(store.transaction);
    });
}

/**
 * Get multiple values by their keys
 */
export function getMany(keys, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readonly', (store) =>
        Promise.all(keys.map((key) => promisifyRequest(store.get(key)))),
    );
}

/**
 * Update a value. This lets you see the old value and update it as an atomic operation.
 *
 */
export function update(key, updater, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore(
        'readwrite',
        (store) =>
            // Need to create the promise manually.
            // If I try to chain promises, the transaction closes in browsers
            // that use a promise polyfill (IE10/11).
            new Promise((resolve, reject) => {
                store.get(key).onsuccess = function () {
                    try {
                        store.put(updater(this.result), key);
                        resolve(promisifyRequest(store.transaction));
                    } catch (err) {
                        reject(err);
                    }
                };
            }),
    );
}

/**
 * Delete a particular key from the store.
 *
 */
export function del(key, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readwrite', (store) => {
        store.delete(key);
        return promisifyRequest(store.transaction);
    });
}

/**
 * Delete multiple keys at once.
 *
 */
export function delMany(keys, getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readwrite', (store) => {
        keys.forEach((key) => store.delete(key));
        return promisifyRequest(store.transaction);
    });
}

/**
 * Clear all values in the store.
 *
 */
export function clear(getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readwrite', (store) => {
        store.clear();
        return promisifyRequest(store.transaction);
    });
}

export function clearAll(excluded) {
    // const request = indexedDB.deleteDatabase(DB_NAME);
    // return promisifyRequest(request)
    return new Promise((resolve, reject) => {
        indexedDB.databases().then(dbs => {
            var promises = dbs.filter(db => db.name !== excluded).map(db => {
                // console.warn(`del db ${db.name}`)
                return new Promise((resolve, reject) => {
                    var req = indexedDB.deleteDatabase(db.name);
                    req.onsuccess = resolve;
                    req.onerror = reject;
                    req.onblocked = reject;
                });
            });
            Promise.all(promises).then(resolve).catch(reject);
        })
    })

}

function eachCursor(store, callback) {
    store.openCursor().onsuccess = function () {
        if (!this.result) return;
        callback(this.result);
        this.result.continue();
    };
    return promisifyRequest(store.transaction);
}

/**
 * Get all keys in the store.
 *
 */
export function keys(getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readonly', (store) => {
        // Fast path for modern browsers
        if (store.getAllKeys) {
            return promisifyRequest(store.getAllKeys());
        }

        const items = [];

        return eachCursor(store, (cursor) =>
            items.push(cursor.key)
    ).then(() => items);
    });
}

/**
 * Get all values in the store.
 *
 */
export function values(getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readonly', (store) => {
        // Fast path for modern browsers
        if (store.getAll) {
            return promisifyRequest(store.getAll());
        }

        const items = [];

        return eachCursor(store, (cursor) => items.push(cursor.value)).then(
            () => items,
        );
    });
}

/**
 * Get all entries in the store. Each entry is an array of `[key, value]`.
 *
 */
export function entries(getStore) {
    if (!getStore) return Promise.reject(STORE_NOT_INIT)
    return getStore('readonly', (store) => {
        // Fast path for modern browsers
        // (although, hopefully we'll get a simpler path some day)
        if (store.getAll && store.getAllKeys) {
            return Promise.all([
                promisifyRequest(
                    store.getAllKeys(),
            ),
                promisifyRequest(store.getAll()),
        ]).then(([keys, values]) => keys.map((key, i) => [key, values[i]]));
        }

        const items = [];

        return getStore('readonly', (store) =>
            eachCursor(store, (cursor) =>
                items.push([cursor.key, cursor.value]),
            ).then(() => items),
        );
    });
}
